import { createComponentFromModel } from 'core/loadEntities';

export default function ennemyLoopLeftRight(entity) {
  const forces = {
    name: 'forces',
    scope: 'common',
    params: {
      parts: {
        moveLeft: {
          x: -250,
          y: 0,
          z: 0,
          rotateX: 0,
          rotateY: 0,
          duration: 3000,
          easing: {
            scope: 'ease',
            name: 'ease-linear',
          },
          ending:{
            scope: 'aJam',
            name: 'timeoutLoopRightLeft',
          },
          elapsed: 0,
        },
      },
    },
  };
  const newForces = createComponentFromModel.call(this, forces);

  const entityForces = entity.get('forces');
  delete entityForces.parts.moveRight;

  const spriteSheets = entity.get('sprites');
  spriteSheets.parts.main.animation.frame=0;
  spriteSheets.parts.main.source = 'ennemy-walk-left'
  spriteSheets.parts.light.source = 'light-idle-left'
  spriteSheets.parts.light.destination.x= -71;

  const hitbox = entity.get('hitbox');
  hitbox.x=-64;

  entityForces.parts.moveLeft = newForces.parts.moveLeft;
}
